6px 2px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.topMost {\r\n    z-index: 5000;\r\n}\r\n\r\n.highlighted {\r\n    animation: glow 0.5s infinite alternate;\r\n    @keyframes glow {\r\n        to {\r\n            border-color: white;\r\n        }\r\n    }\r\n}\r\n\r\n.header-container {\r\n    grid-row: 1;\r\n    grid-column: 1;\r\n    position: relative;\r\n    border: 4px solid black;\r\n    border-top-right-radius: 7px;\r\n    border-top-left-radius: 7px;\r\n    background: black;\r\n    color: white;\r\n    transform: scaleX(1.01) translateY(-0.5px);\r\n    transform-origin: center;\r\n    display: grid;\r\n    grid-template-columns: auto 1fr auto;\r\n    grid-template-rows: 100%;\r\n}\r\n\r\n.headerIcon {\r\n    z-index: 10;\r\n    align-self: center;\r\n    user-select: none;\r\n    display: grid;\r\n    transform: translateY(1px);\r\n    grid-row: 1;\r\n    grid-column: 3;\r\n}\r\n\r\n.headerCollapse {\r\n    grid-row: 1;\r\n    grid-column: 1;\r\n    z-index: 10;\r\n    align-self: center;\r\n    user-select: none;\r\n    display: grid;\r\n    cursor: pointer;\r\n    color: white;\r\n    font-weight: bold;\r\n    font-size: 16px;\r\n    justify-content: center;\r\n    width: 20px;\r\n    transform: rotate(0deg);\r\n    transition: transform 0.2s;\r\n    transform-origin: center;\r\n}\r\n\r\n.collapsed {\r\n    transform: rotate(-90deg);\r\n}\r\n\r\n.header {\r\n    grid-row: 1;\r\n    grid-column: 2;\r\n    font-size: 16px;\r\n    text-align: center;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n    overflow: hidden;\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.connections {\r\n    grid-row: 2;\r\n    grid-column: 1;\r\n\r\n    display: grid;\r\n    grid-template-columns: 50% 50%;\r\n    grid-template-rows: auto auto auto;\r\n    transform: scale(1);\r\n}\r\n\r\n.content {\r\n    min-height: 20px;\r\n    grid-row: 3;\r\n    grid-column: 1;\r\n}\r\n\r\n.comments {\r\n    position: absolute;\r\n    top: -50px;\r\n    width: 200px;\r\n    height: 45